import React, { useRef, useEffect } from 'react';

const RoundGraph = value => {
  const canvas = useRef(null);
  const byRound = value.byRound;

  const sortTable = (a, b) => {
    // 승점 순
    if (a.winningPoint !== b.winningPoint) {
      return b.winningPoint - a.winningPoint;
    } else {
      // 승점이 같을 경우, 다득점
      if (a.getScore !== b.getScore) {
        return b.getScore - a.getScore;
      } else {
        //득점이 같을 경우, 득실차
        if (a.getScore - a.lostScore !== b.getScore - b.lostScore) {
          return b.getScore - b.lostScore - (a.getScore - a.lostScore);
        } else {
          //득실차가 같을 경우, 다승 순
          return b.win - a.win;
        }
      }
    }
  };

  for (let i = 0; i < byRound.length; i++) {
    for (let j = 0; j < byRound[i].length; j++) {
      if (i > 0) {
        byRound[i][j].winningPoint += byRound[i - 1][j].winningPoint;
      }
    }
  }
  for (let i = 0; i < byRound.length; i++) {
    byRound[i].sort((a, b) => sortTable(a, b));
  }

  const getRanking = name => {
    const result = byRound.map(
      item => item.findIndex(match => match.name === name) + 1
    );
    return result;
  };

  const getMaxRanking = arrByRound => {
    let result = 13;
    for (let i = 0; i < arrByRound.length; i++) {
      if (arrByRound[i] < result) {
        result = arrByRound[i];
      }
    }
    return result;
  };

  useEffect(() => {
    const ctx = canvas.current.getContext('2d');
    animate(ctx, byRound, canvas.current.width, canvas.current.height);
  });

  function animate(ctx, byRound, width, height) {
    console.log('a');
    const xLabelHeight = 10;
    const yPaddingTop = 20;
    const xPos = width / (byRound.length + 1);
    const yPosPer = (height - yPaddingTop) / (byRound[0].length + 1);
    let x = xPos;
    let y = 0;

    const minY = getMaxRanking(getRanking('전북')) * yPosPer;
    let yPosArr = getRanking('전북').map(item => item * yPosPer);
    const drawYposArrInit = getRanking('전북').map(
      item => height - xLabelHeight
    );
    //let drawYPosArr = getRanking('전북').map(item => height);
    let tmpArr;

    for (let i = 0; i < height - minY + 1; i++) {
      let drawYposArr;
      if (i === 0) {
        drawYposArr = drawYposArrInit.map(item => item);
      } else {
        drawYposArr = tmpArr.map(item => item);
      }

      for (let j = 0; j < yPosArr.length; j++) {
        //console.log(yPosArr[j] + ':' + drawYposArr[j]);
        if (drawYposArr[j] - yPosArr[j] < 1) {
          drawYposArr[j] = yPosArr[j];
        } else if (drawYposArr[j] > yPosArr[j]) {
          drawYposArr[j] -= 1;
        }
      }
      tmpArr = drawYposArr;
      setTimeout(function() {
        ctx.clearRect(0, 0, width, height);
        drawAxis();
        drawGraph(drawYposArr, '#006633', 10);
      }, i * 5);
    }
    //console.log(drawYposArr);
    console.log(yPosArr);
    console.log(minY);
    function drawAxis() {
      x = xPos;
      y = 0;
      ctx.clearRect(0, 0, width, height);
      // x,y 축 그리기
      ctx.beginPath();
      ctx.moveTo(10, 0);
      ctx.lineTo(10, height - 20);
      ctx.lineTo(width, height - 20);
      ctx.stroke();
      ctx.closePath();

      // x축 레이블 + Y축 가이드
      for (var i = 0; i < byRound.length; i++) {
        ctx.fillText(i + 1, x, height - xLabelHeight);
        ctx.beginPath();
        ctx.lineWidth = 1;
        ctx.strokeStyle = '#D6D6D6';
        ctx.moveTo(x, 0);
        ctx.lineTo(x, height - 20);
        ctx.stroke();
        ctx.closePath();

        x += xPos;
      }

      // y축 레이블 + x축 가이드
      for (let i = 0; i < byRound[0].length; i++) {
        y += yPosPer;
        ctx.fillText(i + 1, 0, y);
        ctx.beginPath();
        ctx.lineWidth = 1;
        ctx.strokeStyle = '#D6D6D6';
        ctx.moveTo(10, y);
        ctx.lineTo(width, y);
        ctx.stroke();
        ctx.closePath();
      }
    }

    function drawGraph(arrY, color, delay) {
      x = xPos;
      ctx.beginPath();

      for (let i = 0; i < arrY.length; i++) {
        if (i > 0) {
          ctx.lineTo(x, arrY[i]);
        } else {
          ctx.moveTo(x, arrY[0]);
        }
        x += xPos;
      }
      ctx.strokeStyle = color;
      //ctx.lineWidth = 4;
      ctx.stroke();
      ctx.closePath();

      x = xPos;

      for (let i = 0; i < arrY.length; i++) {
        ctx.beginPath();
        ctx.arc(x, arrY[i], 4, 0, (Math.PI / 180) * 360, false);
        ctx.fillStyle = color;
        //ctx.lineWidth = 4;
        ctx.fill();
        ctx.closePath();
        x += xPos;
      }
    }
  }
  const canvasWidth =
    document.documentElement.clientWidth > 768
      ? document.documentElement.clientWidth * 0.5
      : document.documentElement.clientWidth * 0.9;

  return (
    <div className="rangking_roundGraph">
      <canvas
        className="roundGraph"
        ref={canvas}
        width={canvasWidth}
        height="500"
      ></canvas>
    </div>
  );
};

export default RoundGraph;
