import React from 'react';
import { useSelector } from 'react-redux';
import MatchInfo from '../components/MatchInfo';

const MatchInfoContainer = () => {
  const match = useSelector(state => state.selectedMatch);
  const year = useSelector(state => state.selectedYear);
  const month = useSelector(state => state.selectedMonth);
  const date = useSelector(state => state.selectedDate);

  return <MatchInfo year={year} month={month} date={date} match={match} />;
};

export default MatchInfoContainer;
